/* purgecss start ignore */

.token-icon {
  width: 48px;
  height: 48px;
}

* {
  box-sizing: border-box;
}

html {
  --bg: #efefef;
  --fg: #ffffff;
  --gray: #555;
  --light-gray: #ccc;
  --shadow: #e6e6e6;
  --success: rgb(90, 206, 132);
  --primary: #FF585D;
  --secondary: #0072CE;
  background-color: #efefef;
  background-color: var(--bg);
  color: #ffffff;
  color: var(--fg);
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica,Arial,sans-serif;
  font-size: calc(0.9em + 0.5vw);
  line-height: 1.3;
}

body {
  margin: 0;
  /* padding: 1em; */
  background-color: #FFFFFF !important;
}

main {
  margin: 0 auto;
  max-width: 25em;
}

h1 {
  background-image: url(/static/media/logo-black.47b91dd0.svg);
  background-position: center 1em;
  background-repeat: no-repeat;
  background-size: auto 1.5em;
  margin-top: 0;
  padding: 2.5em 0 0.5em;
  text-align: center;
}

a,
.link {
  color: #FF585D;
  color: var(--primary);
  text-decoration: none;
}

a:hover,
a:focus,
.link:hover,
.link:focus {
  text-decoration: underline;
}

a:active,
.link:active {
  color: #0072CE;
  color: var(--secondary);
}

button, input {
  font: inherit;
  outline: none;
}

button {
  background-color: #0072CE;
  background-color: var(--secondary);
  border-radius: 5px;
  border: none;
  color: #efefef;
  cursor: pointer;
  padding: 0.3em 0.75em;
  transition: -webkit-transform 30ms;
  transition: transform 30ms;
  transition: transform 30ms, -webkit-transform 30ms;
}

button:hover, button:focus {
  box-shadow: 0 0 10em rgba(255, 255, 255, 0.2) inset;
}

button:active {
  box-shadow: 0 0 10em rgba(0, 0, 0, 0.1) inset;
}

button.link {
  background: none;
  border: none;
  box-shadow: none;
  display: inline;
}

[disabled] button, button[disabled] {
  box-shadow: none;
  background-color: #ccc;
  background-color: var(--light-gray);
  color: gray;
  cursor: not-allowed;
  -webkit-transform: none;
  transform: none;
}

[disabled] button {
  text-indent: -900em;
  width: 2em;
  position: relative;
}

[disabled] button:after {
  content: " ";
  display: block;
  width: 0.8em;
  height: 0.8em;
  border-radius: 50%;
  border: 2px solid #fff;
  border-color: #ffffff transparent #ffffff transparent;
  border-color: var(--fg) transparent var(--fg) transparent;
  -webkit-animation: loader 1.2s linear infinite;
  animation: loader 1.2s linear infinite;
  position: absolute;
  top: 0.45em;
  right: 0.5em;
}

@-webkit-keyframes loader {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg)
  }

  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg)
  }
}

@keyframes loader {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg)
  }

  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg)
  }
}

fieldset {
  border: none;
  padding: 2em 0;
}

input {
  background-color: #e6e6e6;
  background-color: var(--shadow);
  border: none;
  border-radius: 5px 0 0 5px;
  caret-color: #FF585D;
  caret-color: var(--primary);
  color: inherit;
  padding: 0.25em 1em;
}

input::selection {
  background-color: #0072CE;
  background-color: var(--secondary);
  color: #efefef;
}

input:focus {
  box-shadow: 0 0 10em rgba(0, 0, 0, 0.02) inset;
}

code {
  color: #555;
  color: var(--gray);
}

li {
  padding-bottom: 1em;
}

aside {
  -webkit-animation: notify ease-in-out 10s;
  animation: notify ease-in-out 10s;
  background-color: #e6e6e6;
  background-color: var(--shadow);
  border-radius: 5px;
  bottom: 0;
  font-size: 0.8em;
  margin: 1em;
  padding: 1em;
  position: fixed;
  -webkit-transform: translateY(10em);
  transform: translateY(10em);
  right: 0;
}

aside footer {
  display: -webkit-flex;
  display: flex;
  font-size: 0.9em;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  margin-top: 0.5em;
}

aside footer *:first-child {
  color: rgb(90, 206, 132);
  color: var(--success);
}

aside footer *:last-child {
  color: #555;
  color: var(--gray);
}

@-webkit-keyframes notify {
  0% { -webkit-transform: translateY(10em); transform: translateY(10em)
  }

  5% { -webkit-transform: translateY(0); transform: translateY(0)
  }

  95% { -webkit-transform: translateY(0); transform: translateY(0)
  }

  100% { -webkit-transform: translateY(10em); transform: translateY(10em)
  }
}

@keyframes notify {
  0% { -webkit-transform: translateY(10em); transform: translateY(10em)
  }

  5% { -webkit-transform: translateY(0); transform: translateY(0)
  }

  95% { -webkit-transform: translateY(0); transform: translateY(0)
  }

  100% { -webkit-transform: translateY(10em); transform: translateY(10em)
  }
}

@media (prefers-color-scheme: dark) {
  html {
    --bg: #1e1e1e;
    --fg: #efefef;
    --gray: #aaa;
    --shadow: #2a2a2a;
    --light-gray: #444;
  }

  h1 {
    background-image: url(/static/media/logo-white.a3d61d15.svg);
  }

  input:focus {
    box-shadow: 0 0 10em rgba(255, 255, 255, 0.02) inset;
  }
}

